.projects-detailed {
  position: relative;
  padding-bottom: 125px;
  background-color: $background-color;
  &__title {
    z-index: 2;
    position: relative;
    padding-top: 20px;
    width: 412px;
    background: linear-gradient(90deg, $background-color 91.26%, rgba(255, 255, 255, 0) 101.43%);
    @media screen and (max-width: 992px) {
      display: none;
    }
  }
  .projects__controls {
    position: absolute;
    top: 236px;
    right: 0;
    z-index: 2;
    @media screen and (max-width: 1280px) {
      right: 30px;
    }
    @media screen and (max-width: 992px) {
      top: 198px;
    }
    @media screen and (max-width: 780px) {
      left: 17px;
    }
  }
}

.titles-slider {
  position: relative;
  &::after {
    content: '';
    display: block;
    width: 24px;
    height: 2px;
    background-color: $accent-color;
  }
  &__slide {
    padding-top: 27px;
    padding-bottom: 27px;
    min-height: 160px;
    background-color: $background-color;
    h2 {
      font-size: 2.25rem;
      line-height: 1.15;
      font-weight: 800;
      margin: 0;
      @media screen and (max-width: 1919px) {
        font-size: 1.875rem;
      }
    }
  }
}

.titles-control-slider {
  &-wrapper {
    position: absolute;
    width: calc(50% + 220px);
    top: 0;
    left: calc(50% - 220px);
    @media screen and (max-width: 992px) {
      width: 100%;
      left: 0;
      z-index: 1;
    }
  }
  &__slide {
    padding: 29px;
    min-height: 214px;
    background-color: #F2F3F5;
    border: 1px solid #ECECEC;
    border-top: none;
    @media screen and (max-width: 992px) {
      padding: 24px 16px;
      min-height: 176px;
    }
    &.swiper-slide-thumb-active {
      opacity: 0.7;
      @media screen and (max-width: 992px) {
        background-color: #F6F6F8;
        opacity: 1;
        color: $accent-color;
      }
    }
  }
  &__slide-index {
    font-weight: 600;
    color: $secondary-font-color-light;
  }
  &__title {
    font-weight: 700;
    font-size: 1.125rem;
    text-transform: uppercase;
    margin-top: 56px;
    @media screen and (max-width: 1919px) {
      font-size: 0.936rem;
    }
    @media screen and (max-width: 992px) {
      margin-top: 28px;
    }
    @media screen and (max-width: 560px) {
      margin-top: 16px;
    }
  }
}

.body-slider {
  @media screen and (max-width: 992px) {
    padding-top: 200px;
  }
  &__slide {
    padding-top: 53px;
    background-color: $background-color;
    p, div, ul, li {
      max-width: 848px;
      margin: 24px auto;
    }
    img {
      display: block;
      margin: 24px auto;
    }
  }
  &__date {
    font-weight: 600;
    color: $secondary-font-color-light;
    font-size: .875rem;
    width: 848px;
  }
  .share {
    width: 307px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 19px;
    margin: 0;
    &-wrapper {
      margin: 51px auto 0;
    }
    .social-list {
      gap: 9px;
      margin: 0;
    }
    p {
      font-size: 0.875rem;
      color: $secondary-font-color-light;
    }
  }
}

