* {
	box-sizing: border-box;
}

body {
	margin: 0;
}

.container {
	width: 1280px;
	margin: 0 auto;
	@media screen and (max-width: 1280px) {
		padding-left: 30px;
		padding-right: 30px;
		width: 100%;
	}
	@media screen and (max-width: 780px) {
		padding-left: 16px;
		padding-right: 16px;
	}
}

._relative {
	position: relative;
}

.content-container {
		max-width: 848px;
	&_centered {
		margin: 0 auto;
	}
}

img {
	max-width: 100%;
}